<template lang="pug">
  header.header
    .header__cover
    .header__inner
      .container
        .header__wrap
          .header__nav
            nav.nav(v-if="lang === 'RU' || lang === null")
              .nav__item(
                v-for="item in contentMenuRU"
              )
                a.nav__link(
                  :href="item.anchor"
                  @click="initAnimatedAnchor") {{item.name}}
            nav.nav(v-else)
              .nav__item(
                v-for="item in contentMenuEn"
              )
                a.nav__link(
                  :href="item.anchor"
                  @click="initAnimatedAnchor") {{item.name}}
</template>

<script>
import {throttle} from 'lodash-es';
export default {
  name: 'Header',
  data() {
    return {
      contentMenuEn: {
        1: {
          name: 'Into',
          anchor: '#intro',
        },
        2: {
          name: 'About me',
          anchor: '#about',
        },
        3: {
          name: 'Skills',
          anchor: '#skills',

        },
        4: {
          name: 'Portfolio',
          anchor: '#portfolio',
        },
        5: {
          name: 'Contacts',
          anchor: '#footer',
        },
      },
      contentMenuRU: {
        1: {
          name: 'Интро',
          anchor: '#intro',
        },
        2: {
          name: 'Обо мне',
          anchor: '#about',
        },
        3: {
          name: 'Скиллы',
          anchor: '#skills',

        },
        4: {
          name: 'Портфолио',
          anchor: '#portfolio',
        },
        5: {
          name: 'Контакты',
          anchor: '#footer',
        },
      },
    }
  },
  props: {
    lang: null,
  },
  mounted() {
    this.initScrollHeader();
  },
  methods: {
    initAnimatedAnchor(e) {
      e.preventDefault();
      const href = e.currentTarget.getAttribute("href");
      const offsetTop = document.querySelector(href).offsetTop;
      scroll({
        top: offsetTop,
        behavior: "smooth"
      });
    },
    initScrollHeader() {
      const header = document.querySelector('.header');
      document.addEventListener('scroll', throttle(() => {
        const st = window.pageYOffset;
        if (st >= 50) {
          header.classList.add('header--fixed');
        } else {
          header.classList.remove('header--fixed')
        }
      },150))
    }
  }
}
</script>
